/* el "_", indica que se va incluido en otro.*/

@use 'base/variables' as v;
@use 'base/mixins' as m;
.header {
    padding: 5rem 0;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    @include m.tablet {
        min-height: 70rem;
    }
    @include m.telefono {}
    &::before {
        content: "";
        background-color: v.$negro;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: .6;
    }
    .contenido-header {
        position: relative;
        h1 {
            text-align: center;
            color: v.$blanco;
            text-transform: uppercase;
            margin-top: 5rem;
            @include m.tablet {
                margin-top: 10rem;
            }
        }
    }
}


